<template>
<div id="reback">
  <Row type="flex" align="middle" class-name="titleline">
    <Col span="4">
      <span class="helptitle"></span>
    </Col>
    <Col class-name="more" offset="16" span="4">
    <router-link :to="{path: '/helpcenter/back'}">
<!--       <span> >></span>
 -->    </router-link>
    </Col>
  </Row>
  <div class="helpcard answercard">
    <Row type="flex" align="middle">
      <Col span="2">
        <img class="avatar" src="./avatar.png">
      </Col>
      <Col span="4">
        <input class="avatartext" placeholder=""  v-model="reback.user"></input>
      </Col>
    </Row>
    <div class="innerhr"></div>
    <textarea type="textarea"  placeholder="..." :rows="6" class="helpinput" v-model="reback.message"></textarea>
    <Button type="info" class="helpbtn"></Button>
  </div> 
</div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      reback: {
        user: '',
        message: ''
      }
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.titleline
  margin-bottom 15px
.helptitle
  color rgba(153, 153, 153, 0.84)
  font-size 19px
  height 22px
.helpcard
  background-color #fff
  min-height 120px
  margin-top 3px
  border-radius 4px
  padding 13px
.answercard
  height 250px
.avatar 
  width 30px
  margin-left 2px
.avatartext
  font-size 16px
  position relative
  bottom 2px
  color #707070
  outline none
  width 500px
.innerhr
  border-top 1px solid #dadada
  margin-top 6px
.helpinput
  margin-top 10px
.helpinput
  resize none
  height 130px
  width 100%
  border 1px solid transparent
  font-size 15px
.helpinput:focus
  outline none
.ivu-btn-info
  background-color #00a1e9
  height 30px 
  width 85px
  border-radius 2px
.helpbtn
  float right
  margin-top 10px
.more
  color #00a1e9
.more span 
  float right
  cursor pointer
</style>



