<template>
  <div class="surveylist">
  <Form :model="formDynamic">
    <div v-for = "(list, index) in formDynamic.lists" class="questionwrapper">
      <p class="questiontopic">
        <span v-if="flag">{{index + 1}}.</span>
        {{list.question.topic}}
        <span v-if="list.question.isrequired" class="bida"></span>
      </p>
      <div class="questioninner">
        <p class="questionremark">{{list.question.remark}}</p>
        <div v-if = "list.question.qtype === '0'||list.question.qtype === 0">
          <RadioGroup v-model="list.answer" @on-change="change(index, 0)" vertical>
            <Radio v-for="option in list.options" :label="option.okey">{{option.option}}</Radio>
          </RadioGroup>
        </div>
        <div v-if = "list.question.qtype === '1'||list.question.qtype === 1">
          <CheckboxGroup @on-change="change(index)" v-model="list.answer" vertical>
            <Checkbox v-for ="option in list.options" :label="option.okey" >
              <span>{{option.option}}</span>
            </Checkbox>
          </CheckboxGroup>
          <p class="notice">
            <span v-if="list.question.max != null">
              {{list.question.max}}
            </span>
            <span v-if="list.question.min != null">
              {{list.question.min}}
            </span>
          </p>
        </div>

        <div v-if = "list.question.qtype === '10'||list.question.qtype === 10">
          <i-select v-model.sync="list.answer.st" style="width: 49%; float: left; margin-right: 2%; max-heght:100px; overflow-y: auto;" @on-change="change1(index)">
            <i-option v-for="(item1, index1) in getopt1(list.options)" :value="item1.value">{{item1.label}}</i-option>
          </i-select>
          <i-select style="width: 49%; float:right;" v-model.sync="list.answer.nd"  @on-change="change(index)">
            <i-option v-for="(item2, index2) in getopt2(list.options, list.answer.st)" :value="item2.value">{{item2.label}}</i-option>
          </i-select>
          <!-- {{list}} -->
        </div>        

        <div v-if = "list.question.qtype === '3'||list.question.qtype === 3">
          <FormItem v-if="list.question.test === null||list.question.test === 0">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
          </FormItem>
          <FormItem v-if="list.question.test === 1">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!telphonetest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 2">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!numbertest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 3">
            <Input  @on-change="change(index)" v-model = "list.answer" ></Input>
            <p class="notice" v-if="true && list.answer === '' ">2001-01-01</p>
            <p class="notice" v-if="list.answer != '' && (!datetest(list.answer))"> 2001-01-01</p>
          </FormItem>
          <FormItem v-if="list.question.test === 4">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!emailtest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 5">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!chinesetest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 6">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!englishtest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 7">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!weburltest(list.answer))"></p>
          </FormItem>
          <FormItem v-if="list.question.test === 8">
            <Input  @on-change="change(index)" v-model = "list.answer"></Input>
            <p class="notice" v-if="true && list.answer === '' "></p>
            <p class="notice" v-if="list.answer != '' && (!idcardtest(list.answer))"></p>
          </FormItem>
        </div>

        <div v-if = "list.question.qtype === '4'||list.question.qtype === 4">
          <Input @on-change="change(index)" v-model = "list.answer" type="textarea" :rows="4"></Input>
        </div>

        <div v-if = "list.question.qtype === '5'||list.question.qtype === 5">
          <Row>
            <Col align="left" span="3"><span class="starlable"></span></Col>
            <Col class-name="star" align="middle" span="18">
<!--             <FormItem
        :prop="list.answer"
        :rules="{required: list.question.isrequired, message: '', trigger: 'change'}">
 -->              <RadioGroup @on-change="change(index)" v-model="list.answer">
               <Radio :key="n" v-for="(n, index) in nums" v-if="index < list.question.srange" :label="n">
                  <span>{{n}}</span>
                </Radio>
              </RadioGroup>
              <!-- </FormItem> -->
            </Col>
            <Col align="right" span="3"><span class="starlable"></span></Col>
          </Row>
        </div>

        <div v-if = "list.question.qtype === '6'||list.question.qtype === 6">
          <draggable v-model="list.options" :options="{sort:true, animation: 100}" @start="drag=true" @end="checkMove(list.options, index)">
          <div class="move" v-for="option in list.options">
            <div class="moveborder">
              <span class="my-handle">
              <img src="./move.png">
              </span>
              {{option.option}}
            </div>
          </div>
          </draggable>
        </div>

      </div>
    </div>
    </Form>
  </div>
</template>

<script type="text/ecmascript-6">
import draggable from 'vuedraggable'
export default {
  components: {
    draggable
  },
  props: [ 'flag' ],
  data () {
    return {
      bb: '',
      animal: '0',
      nums: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      vertical: 'apple',
      test: '',
      answers: []
      // ruleInline: {
      //   user: [
      //     { 
      //       required: , 
      //       message: 'Please fill in the user name', 
      //       trigger: 'blur' }
      //   ],
        // password: [
        //   { 
        //     required: true, 
        //     message: 'Please fill in the password.', 
        //     trigger: 'blur' 
        //   },
        //   { 
        //     type: 'string', 
        //     min: 6, 
        //     message: 'The password length cannot be less than 6 bits', 
        //     trigger: 'blur' 
        //   }
        // ]
      // }
    }
  },
  methods: {
     numbertest (string) {
        var reg = /^[0-9]+\.?[0-9]*$/
        return reg.test(string)
    },
    telphonetest (string) {
        var reg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/
        return reg.test(string)
    },
    chinesetest (string) {
        var reg = /^[\u4e00-\u9fa5]{0,}$/
        return reg.test(string)
    },
    englishtest (string) {
        var reg = /^[A-Za-z]+$/
        return reg.test(string)
    },
    weburltest (string) {
        var reg = /^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/
        return reg.test(string)
    },
    idcardtest (string) {
        var reg = /^([0-9]){18}(x|X)?$/
        return reg.test(string)
    },
    datetest (string) {
        var reg = /^\d{4}-\d{1,2}-\d{1,2}/
        return reg.test(string)
    },
    emailtest (string) {
        var reg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
        return reg.test(string)
    },
    getopt1 (options) {
      let data = []
      let n1 = -1
      for (let key in options) {
        let o = options[key].option.split('-')
        if ((n1 === -1) || (o[0] != data[n1].label)) { 
          n1++
          data[n1] = {
            value: o[0],
            label: o[0],
            children: []
          }
        }
      }
      return data      
    },
    getopt2 (options, answer) {
      let data = []
      let n1 = -1
      for (let key in options) {
        let o = options[key].option.split('-')
        // console.log(o[0]+'djskflsd'+this.select1)
        if (o[0] == answer) { 
          n1++
          data[n1] = {
            value: o[1],
            label: o[1]
          }
        }
      }
      return data      
    },
    checkMove: function (arr, index) {
      var ss = []
      for (var i = 0; i < arr.length; i++) {
        ss[i] = arr[i].okey
      }
      this.answers[index] = {qnid: this.formDynamic.lists[index].question.qnid, qid: this.formDynamic.lists[index].question.qid, answer: ss}
      this.$store.commit('answers', this.answers)
      console.log(this.answers)
    },
    change (index) {
        this.answers[index] = {
          qnid: this.formDynamic.lists[index].question.qnid, 
          qid: this.formDynamic.lists[index].question.qid, 
          answer: this.formDynamic.lists[index].answer
        }
      this.$store.commit('answers', this.answers)
      // console.log('hhh')
      // console.log(this.answers)
    },
    change1 (index) {
      this.formDynamic.lists[index].answer.nd = ''
        this.answers[index] = {
          qnid: this.formDynamic.lists[index].question.qnid, 
          qid: this.formDynamic.lists[index].question.qid, 
          answer: this.formDynamic.lists[index].answer
        }
      this.$store.commit('answers', this.answers)
    }    
  },
  computed: {
    formDynamic: function () {
      return {lists: this.$store.getters.getquestions}
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.questionwrapper
  font-size 14px
  margin-top 30px
.questionremark
  font-size 12px
  margin-bottom 13px
.questioninner
  margin 0 12px
.questiontype
  color #00a1e9
.upinput
  margin-bottom 10px
.ivu-input input
  height 26px !important
.starlable
  color #999
  font-size 11px
.move
  cursor move
  min-width 50%
.my-handle img
  width 20px
  position relative
  top 5px
.moveborder
  border 1px solid #dddee1
  height 32px
  width 100%
// .ivu-radio-group label
//   padding-left 20px
//   margin 20px 0
.tablerow
  border-bottom 1px solid #dddee1
  height 60px
.tablerow label
  padding-left 25px
.star label
  padding-left 25px
.listinput
  border-bottom 1px solid black
  display inline-block
.listinput:focus
  outline none
.table th,.table td
  padding 20px
  vertical-align middle
  text-align center
  border-bottom 1px solid #dedede
.questiontopic
  font-size 17px
.questionremark
  font-size 14px
  margin-left 10px
.table
  width 100%
.radioclass
  padding 10px 0
.bida
  color #00a1e9
  font-size 12px
.notice
  font-size 12px
  color #ff9277
</style>



